<template>
  <div class="container">
    <div class="search">
        <div class="input-field">
            <input class="default" v-model="departure_code" placeholder="Departure code">
            <input class="default" v-model="arrival_code" placeholder="Arrival code">
            <input class="default" v-model="date" placeholder="Date">
             <input class="default" v-model="fly_class" placeholder="Class">
            <div class="error">{{error}}</div>
        </div>
            <button class="btn btn-search" @click.prevent="search">Search</button>
            <div v-if="loading">Loading...</div>
            <div v-else>
                <Offer v-for="(offer, index) in offers" :offer="offer" :key="index"/>
            </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Offer from './Offer.vue'
export default {
   data() {
    return {
    apiLink:'https://api-stage.etm-system.com/api/login/',
    apiKey: '8e71ac9f18',
    departure_code: '',
    arrival_code: '',
    fly_class: '',
    date: '',
    error: '',
    currency: '',
    loading: false,
    authKey: null,
    request_id: null,
    offers: null
  }
},
  methods: {
    getAuth() {
      axios
        .get(this.apiLink + this.apiKey)
        .then(response => {
            this.error = '';
            this.currency = response.data.currency;
            this.locale = response.data.locale;
            this.authKey = response.data.etm_auth_key;
        })
        .catch(error => {
            this.error = 'I cant auth';
        });
    },
       search(){
        this.loading = true;
        let headers = {
            'etm-auth-key': this.authKey
        }
        let body = {
            "directions": [
                {
                    "departure_code": this.departure_code,
                    "arrival_code": this.arrival_code,
                    "date": this.date
                }
            ],
            "adult_qnt": 1,
            "class": this.fly_class,
            "fare_types": [
                "PUB",
                "NEG"
            ]
        } 

        let url = 'https://api-stage.etm-system.com/api/air/search';
        axios.post(url, body,
            { headers: headers}
        )
        .then(response => {
            this.request_id = response.data.request_id;
            this.getOffer();
        }
        )
    },
    getOffer(){
        let headers = {
            'etm-auth-key': this.authKey
        }
        axios
            .get('https://api-stage.etm-system.com/api/air/offers?request_id=' + this.request_id,
                  { headers: headers}
            )
            .then(response => {
                let status = response.data.status;
                if(status == "InProcess"){
                    setTimeout(() => { this.getOffer();}, 1000)
                }
                else{
                    this.loading = false;
                    this.offers = response.data.offers;
                }
            })
            .catch(error => {
                this.error = 'Some error';
            });
      }
    },
    mounted(){
        this.getAuth();
    },
    components: {
        Offer
    },
}
</script>

<style> 
body{
    background-color: #fff;
    min-width: 320px;
    margin: 0;
    height: 100vh;
}
h1, h2 {
  font-weight: normal;
}
h3{
    font-size: 28px;
    margin-top: 15px
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.container{
    max-width: 800px;
    margin: 0px auto;
    padding: 10px;
}
input.default{
    margin: 5px;
    border: 1px solid #777;
    border-radius: 4px;
    padding: 10px 10px;
    font-size: 16px;
    box-shadow: rgba(0,0,0,.05) 0 5px 20px;
    letter-spacing: 0;
    width: 205px;
    color: #333;
    font-weight: 500;
}
.btn{
    display: inline-flex;
    overflow: hidden;
    margin: 5px;
    padding: 10px 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all 150ms linear;
    text-align: center;
    white-space: nowrap;
    text-decoration: none !important;
    text-transform: none;
    text-transform: capitalize;
    color: #fff;
    border: 0 none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.3;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 5px 10px #e4e4e4;
}
.btn:hover {
    transition: all 150ms linear;
    opacity: .85;
}
.btn:disabled{
    opacity: .65;
    cursor: default;
}
.btn.btn-search{
    background: #416dea;
}
.input-field{
    display: inline-block;
}
.error{
    color: #ff0202;
    display: table;
    height: 20px;
}
@media (max-width: 500px) {
    .input-field{display: block;}
}
</style>



