<template>
	<div class="offer">
		<div class="airline" @click.prevent="showOffers">
			<div class="airline-logo"><img width="100px" height="50px" :src=offer.carrier_logo></div>
	        <div class="airline-name">{{offer.carrier_name}}</div>
	    </div>
	    <div v-if="airlineOffers" class="offer-container">
	    	<table>
	    		<tr>
				    <th>From</th>
				    <th>To</th>
				    <th>Arrival date</th>
				    <th>Departure date</th>
				    <th class="sort" @click.prevent="sortedArray">Duration</th>
				    <th>Price</th>
			   	</tr>
			   	<tbody>
			   		<tr v-for="(fly, index) in offer.offers[0].segments" :key="index">
			   			<td>{{fly.arrival_airport}}</td>
			   			<td>{{fly.departure_airport}}</td>
			   			<td>{{fly.arrival_date}}</td>
			   			<td>{{fly.departure_date}}</td>
			   			<td>{{fly.duration_formated}}</td>
			   			<td>{{fly.price_details[0].total}}</td>
			   		</tr>
			   	</tbody>
	    	</table>
	    </div>
    </div>
</template>
<script>
export default {
	props: ['offer'],
	data() {
        return {
          airlineOffers: false,
          sortedOffers: null,
        }
    },
	methods: {
        showOffers() {
        	this.airlineOffers = !this.airlineOffers;
        },
        sortedArray: function() {
		      function compare(a, b) {
		        if (a.duration_minutes < b.duration_minutes){
		          return -1;
		        }
		        if (a.duration_minutes > b.duration_minutes){
		          return 1;
		        }
		        return 0;
		      }

		      return this.offer.offers[0].segments.sort(compare);
		      this.offer = sortedArray;
		    }
    },
}
</script>
<style>
	.airline{
		text-align: center;
		display: inline-block;
		max-width: 320px;
		cursor: pointer;
	}
	.airline-logo{
		float: left;
	}
	.airline-name{
		float: left;
		margin-top: 17px;
		margin-left: 15px;
	}
	.offer-container{
		max-height: 200px;
		overflow: auto;
	}
	table{
		width: 100%;
	}
	table tr td{
		border: 1px solid #ddd;
	}
	th.sort{
		cursor: pointer;
	}
</style>


